<template>
  <div class="nodeList-container">
    <div class="add" @click="addStatus = true">
      添加节点
    </div>
    <el-dialog width="550px" :visible.sync="addStatus" title="添加节点">
      <el-form label-width="80px">
        <el-form-item class="width" label="节点名称">
          <el-input v-model="form.nodeName" />
        </el-form-item>
        <el-form-item class="width" label="排序">
          <el-input v-model="form.nodeSort" />
        </el-form-item>
        <el-form-item label="所属父级">
          <el-cascader @change="onChange" v-model="form.pid" :options="nodeList" :props="{ checkStrictly: true, label:'name',value:'id', children:'_child' }" clearable />
        </el-form-item>
        <el-form-item class="width" label="节点">
          <el-input v-model="form.nodePath" />
        </el-form-item>
        <el-form-item label="路径">
          <el-input class="width" v-model="form.folder" />
        </el-form-item>
        <el-form-item label="权限">
          <el-select clearable v-model="currentPermisson">
            <el-option v-for="(item,index) of permission" :key="index" :label="item.label" :value="item.value" />
          </el-select>
        </el-form-item>
      </el-form>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="close">取 消</el-button>
        <el-button
          type="primary"
          @click="add"
        >确 定</el-button>
      </span>
    </el-dialog>
    <el-dialog width="550px" :visible.sync="modifyStatus" title="修改节点">
      <el-form label-width="80px">
        <el-form-item class="width" label="节点名称">
          <el-input v-model="form.nodeName" />
        </el-form-item>
        <el-form-item class="width" label="排序">
          <el-input v-model="form.nodeSort" />
        </el-form-item>
        <el-form-item label="所属父级">
          <el-cascader @change="onChange" v-model="form.pid" :options="nodeList" :props="{ checkStrictly: true, label:'name',value:'id', children:'_child' }" clearable />
        </el-form-item>
        <el-form-item class="width" label="节点">
          <el-input v-model="form.nodePath" />
        </el-form-item>
        <el-form-item label="路径">
          <el-input class="width" v-model="form.folder" />
        </el-form-item>
        <el-form-item label="权限">
          <el-select clearable v-model="currentPermisson">
            <el-option v-for="(item,index) of permission" :key="index" :label="item.label" :value="item.value" />
          </el-select>
        </el-form-item>
      </el-form>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="close">取 消</el-button>
        <el-button
          type="primary"
          @click="modify"
        >确 定</el-button>
      </span>
    </el-dialog>
    <div class="nodeList">
      <el-table
        :data="nodeList"
        style="width: 100%;margin-bottom: 20px;"
        row-key="id"
        border
        :tree-props="{children: '_child', hasChildren: 'hasChildren'}"
        :header-cell-style="{ 'text-align': 'center', color: 'black' }"
        :cell-style="{ 'text-align': 'center' }"
      >
        <el-table-column prop="name" label="菜单名称" />
       
        <el-table-column
          prop="sort"
          label="排序"
        />
        <el-table-column prop="desStatus" label="状态" />
        <el-table-column
          prop="url"
          label="节点"
        />
        <el-table-column
          prop="folder"
          label="路径"
        />
        <el-table-column
          prop="created_at"
          label="创建时间"
        />
        <el-table-column width="250px">
          <template
            slot="header"
            :slot-scope="scope"
          >
            操作
          </template>
          <template slot-scope="scope">
            <el-button
              size="mini"
              @click="showModify(scope.row.id)"
            >
              修改
            </el-button>
            <el-switch
              style="margin-left: 30px"
              v-model="scope.row.status"
              active-text="启用"
              inactive-text="禁用"
              @change="onSwtich(scope.row.id)"
            />
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
export default {
    data() {
        return {
            scope:"scope",
            addStatus:false,
            modifyStatus:false,
            nodeList:[],
            currentPermisson:"",
            permission:[
              {
                label:"编辑",
                value:"edit"
              },
              {
                label:"查看",
                value:"view"
              }
            ],
            form:{
                nodeName:"",
                nodeSort:"",
                // 父级节点id
                pid:0,
                // 节点
                nodePath:"",
                modifyId:"",
            }
           

        };
    },
    created() {
        this.getNodeList();
    },
    methods: {
      forMart (nodeList){
        return nodeList.map(item => {
          if(item._child && item._child.length){
            return Object.assign(item, {
                            status: item.status === 1 ? true : false,
                            desStatus: item.status === 1 ? "启用" : "禁用",
                            _child:this.forMart(item._child)
                        });
          }
          return Object.assign(item, {
                            status: item.status === 1 ? true : false,
                            desStatus: item.status === 1 ? "启用" : "禁用",
                        });
        });

      },
        // 获取节点列表
        async getNodeList (){
            try {
                let res = await this.axios.get("/admin/getnode");
                this.nodeList = this.forMart(res.data);
                
            } catch (error) {
                console.log(error);
            }
        },
        // 添加节点
        async add (){
            try {
                let res = await this.axios.post("/admin/addnode",{
                    "name":this.form.nodeName,
                    "sort":this.form.nodeSort,
                    "pid":this.form.pid,
                    "path":this.form.nodePath,
                    "folder":this.form.folder,
                    "permission":this.currentPermisson
                });
                this.close();
                this.$message.success({
                    message:res.msg
                });
                this.getNodeList();
                 
            } catch (error) {
                console.log(error);
            }

        },
        showModify(id){
            this.form.modifyId = id;
            this.modifyStatus = true;
        },
        onChange (value){
            this.form.pid = value[value.length - 1];
            
        },
        // 修改节点
        async modify (){
            try {
                let res = await this.axios.post("/admin/updatenode",{
                    "node_id":this.form.modifyId,
                    "name":this.form.nodeName,
                    "pid":this.form.pid,
                    "path":this.form.nodePath,
                    "sort":this.form.nodeSort,
                    "folder":this.form.folder,
                    "permission":this.currentPermisson
                });
                this.close();
                this.$message.success({
                    message:res.msg
                });
                this.getNodeList();

                 
            } catch (error) {
                console.log(error);
            }

        },
        // 启用 禁用
        async onSwtich (id){
          try {
            await this.axios.post("/admin/updateNodeState",{
              "node_id":id
            });
            this.getNodeList();
          } catch (error) {
            console.log(error);
          }

        },
        close (){
            this.addStatus = false;
            this.modifyStatus = false;
            this.form = {};
            this.currentPermisson = "";
        }
        
    },
    components: {

    }
};
</script>

<style lang="scss" scoped>
.nodeList-container{
    display:flex;
    flex-direction: column;
    .add{
        align-self: flex-end;
        border: 1px solid #F7961C;
        padding: 7px 20px;
        border-radius: 4px;
        margin: 15px 0;
        cursor: pointer;
        
    }
    .width{
        width: 350px;
    }
}
</style>
